import { Meteor } from 'meteor/meteor';
import { Mongo } from 'meteor/mongo';

import { Practitioners } from 'meteor/clinical:hl7-resource-practitioner';

// INSECURE; DELETE ME
if (Meteor.isClient){
  Meteor.subscribe('userDirectory');
}

// INSECURE; DELETE ME
if (Meteor.isServer){
  Meteor.startup(function(){
      console.log('Continuity of Care Startup')

      Meteor.publish("userDirectory", function (argument){          
        if (this.userId) {
            var currentUser = Meteor.users.findOne({_id: this.userId });
            process.env.DEBUG && console.log('Checking currently logged in user while publishing the UserDirectory...');
            process.env.DEBUG && console.log(currentUser);

            if(currentUser.roles.includes('practitioner')){
                return Meteor.users.find();
            }
            if(currentUser.roles.includes('sysadmin')){
                return Meteor.users.find();
            }
          } else {
            return [];
          } 
      });
  })
}