Session.setDefault('timelineDialogOpen', false);
Session.setDefault('activeTimelineResource', false);
Session.setDefault('activeTimelineResourceType', false);
Session.setDefault('activeTimelineResourceDescription', false);
      
export class TimelineSidescrollPage extends React.Component {
  constructor(props) {
    super(props);
  }
  componentWillMount(){
    const timeLineEvents = {};
    // this.timeline = (<EventsTimeline events={timeLineEvents}/>)
    this.timeline = (<Timeline 
        selectHandler={ this.openDialog }
        changedHandler={this.preventRedraw }
        clickHandler={this.preventRedraw }
        options={this.data.options} 
        items={this.data.items} 
        groups={this.data.groups} 
        events={timeLineEvents}
        />)

  }
  getMeteorData() {
    var currentPatient = Patients.findOne();
    console.log('currentPatient', currentPatient)



    let data = {
      timelineDialogOpen: Session.get('timelineDialogOpen'),
      options: {
        width: '100%',
        autoResize: true,
        height: (Session.get('appHeight') - 128)+ 'px',
        dataAttributes: ['id', 'mongoId'],
        stack: true,
        showMajorLabels: false,
        showCurrentTime: true,
        zoomMin: 1000000,
        format: {
          // ??
          // ??
        },

        start: '1978-01-25',  // birthdate
        end: moment().format("YYYY-MM-DD"), //now
        min: '1977-04-25',  // birthdate - 9mo
        max: '2050-01-01',  // birthdate +76 yrs

        
        // start: '1978-01-25',  // birthdate
        // end: moment().format("YYYY-MM-DD"), //now
        // min: '1977-04-25',  // birthdate - 9mo
        // max: '2050-01-01',  // birthdate +76 yrs

        // // TODO:  Clinicals on FHIR Usecase
        // // Need to generalize the timeline function instead of hardcoding examples
        // start: '1990-07-04',
        // end: moment().format("YYYY-MM-DD"),
        // min: '1989-10-04',
        // max: '2066-07-04', 

        groupOrder: 'content'  // groupOrder can be a property name or a sorting function'
      },
      groups: [],
      items: dataSet.get(),
      currentPatient: null,
      activeTimelineResource: Session.get('activeTimelineResource'),
      activeTimelineResourceType: Session.get('activeTimelineResourceType'),
      activeTimelineResourceDescription: Session.get('activeTimelineResourceDescription')

    }
    
    // if(this.props.birthdate){
    //   data.options.start = this.props.birthdate;
    // }

    if(get(currentPatient, 'birthDate')){
      data.options.start = moment(currentPatient.birthDate).format('YYYY-MM-DD');  // birthdate
      data.options.end = moment().format("YYYY-MM-DD"); // current date
      data.options.min = moment(currentPatient.birthDate).subtract(270, 'days').format('YYYY-MM-DD');  // birthdate - 9mo // conception date
      data.options.max = moment(currentPatient.birthDate).add(78, 'years').format('YYYY-MM-DD');  // birthdate +76 yrs  // estimated death date
    }

    var genderColor;

    if(get(currentPatient, 'gender')){
      if(get(currentPatient, 'gender') === 'male'){
        genderColor = '#d5ddf6';
      }
      if(get(currentPatient, 'gender') === 'female'){
        genderColor = '#ecdcde';
      }
    }

    if(Session.get('gender') === 'Pink'){
      genderColor = '#ecdcde';
    } else if (Session.get('gender') === "BabyBlue"){
      genderColor = '#d5ddf6';
    }

    if(Meteor.userId()){
      var continuityOfCare = get(Meteor.user(), 'profile.continuityOfCare', {});

      if(get(currentPatient, 'birthDate')){

        var inUtero = {
          mongoId: 'utero',
          id: 'utero',
          start: moment(currentPatient.birthDate).subtract(270, 'days').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('utero')){
          dataSet.add(inUtero)   
        }

        // puberty
        var puberty = {
          mongoId: 'puberty',
          id: 'puberty',
          start: moment(currentPatient.birthDate).add(10, 'years').format('YYYY-MM-DD'),
          end: moment(currentPatient.birthDate).add(17, 'years').format('YYYY-MM-DD'),
          type: 'background',
          style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        }
        // in-utero
        if(!dataSet.get('puberty')){
          dataSet.add(puberty)   
        }


        // // menarche
        // var menarcheDate = {
        //   start: '1988-07-04',
        //   type: 'background',
        //   style: 'background-color: ' + genderColor + '; opacity: 0.5;'
        // }
        // dataSet.add(menarcheDate)   

        // // in-utero
        // dataSet.add({
        //   start: '1989-10-04',
        //   end: '1990-07-04',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

        // // puberty
        // dataSet.add({
        //   start: '2000-07-04',
        //   end: '2007-07-04',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

        // // pregnancy
        // dataSet.add({
        //   start: '2017-04-15',
        //   end: '2018-01-31',
        //   type: 'background',
        //   style: 'background-color: #ecdcde; opacity: 0.5;'
        // })   

      }
      
      AllergyIntolerances.find().forEach(function(allergy){
        //console.log('allergy', allergy);
        if(allergy){
          //console.log('allergyId', get(allergy, 'id'))
          //console.log('allergy_id', get(allergy, '_id'))
          //console.log('dataSet.get(id)', dataSet.get(get(allergy, 'id')))
          //console.log('dataSet.get(_id)', dataSet.get(get(allergy, '_id')))
          //console.log('dataSet.getIds()', dataSet.getIds())

          if(!dataSet.get(get(allergy, 'id'))){
            dataSet.add({
              mongoId: get(allergy, '_id', ''),
              id: get(allergy, '_id', ''),
              content: get(allergy, 'reaction[0].manifestation[0].text'),
              group: 'Allergies',
              start: get(allergy, 'reaction[0].onset', moment()),
              end: null,
              type: 'point'
            })     
          }
        }
      });  
      CarePlans.find().forEach(function(carePlan){
        //console.log('carePlan', carePlan);
        if(carePlan){
          var startDate = get(carePlan, 'period.start', moment());
          var endDate = get(carePlan, 'period.end', moment());
  
          if(get(carePlan, 'addresses')){
            // is earlier
            carePlan.addresses.forEach(function(conditionReference){
              Conditions.find({'code.text': conditionReference.display}).map(function(condition){
                if(moment(condition.onsetDateTime).isBefore(startDate)){
                  startDate = condition.onsetDateTime;
                }
              })
            })
          }
  
          if(get(carePlan, 'activity')){
            carePlan.activity.forEach(function(activity){      
              var newActivity = {
                mongoId: get(carePlan, '_id', ''),
                id: get(carePlan, 'id') ? get(carePlan, 'id') : get(carePlan, '_id', ''),
                content: get(activity, 'detail.code.text') ? get(activity, 'detail.code.text') : get(activity, 'description'),
                group: 'CarePlans',
                start: startDate,
                type: 'point',
                style: "background-color: white; border-color: lightgray;"
              }
              
              if(get(carePlan, 'category[0].text') === "Longitudinal"){
                newActivity.type = "range",
                newActivity.end = moment(); 
              }
              if(carePlan.period){
                newActivity.type = "range",
                newActivity.end = get(carePlan, 'period.end');            
              }
              if(!dataSet.get(get(carePlan, 'id'))){
                dataSet.add(newActivity)   
              }
            });      
          }  
        }
      })
      Conditions.find().forEach(function(condition){
        //console.log('condition', condition);
        if(condition && get(condition, 'onsetDateTime')){
          let conditionItem = {
            group: 'Conditions',
            mongoId: get(condition, '_id', ''),
            id: get(condition, 'id', ''),
            content: get(condition, 'identifier[0].value') ? get(condition, 'identifier[0].value', '') : get(condition, 'code.coding[0].display', ''),
            start: moment(get(condition, 'onsetDateTime')).format("YYYY-MM-DD"),
            type: 'point'
          }
          if(get(condition, 'abatementDateTime')){
            conditionItem.end = moment(get(condition, 'abatementDateTime')).format("YYYY-MM-DD");
            conditionItem.style = "background-color: white; border-color: lightgray;";
            conditionItem.type = 'range';
          }

          // console.log('conditionId', get(condition, 'id'))
          // console.log('condition_id', get(condition, '_id'))
          // console.log('dataSet.get(id)', dataSet.get(get(condition, 'id')))
          // console.log('dataSet.get(_id)', dataSet.get(get(condition, '_id')))
          // console.log('dataSet.getIds()', dataSet.getIds())
          console.log('dataSet.get()', dataSet.get())

          if(!dataSet.get(get(condition, 'id'))){
            dataSet.add(conditionItem);   
          }
        }
      });  
      Immunizations.find().forEach(function(immunization){
        //console.log('immunization', immunization);
        if(immunization){
          if(!dataSet.get(get(immunization, 'id'))){
            dataSet.add({
              mongoId: get(immunization, '_id', ''),
              id: get(immunization, 'id') ? get(immunization, 'id') : get(immunization, '_id', ''),
              content: get(immunization, 'vaccineCode.text'),
              group: 'Immunizations',
              start: get(immunization, 'date'),
              end: null,
              type: 'point'
            })     
          }
        }
      });   
      MedicationOrders.find().forEach(function(medicationOrder){
        //console.log('medicationOrder', medicationOrder);
        if(medicationOrder){
          if(!dataSet.get(get(medicationOrder, 'id'))){
            dataSet.add({
              mongoId: get(medicationOrder, '_id', ''),
              id: get(medicationOrder, 'id') ? get(medicationOrder, 'id') : get(medicationOrder, '_id', ''),
              content: get(medicationOrder, 'identifier[0].value'),
              group: 'MedicationOrders',
              start: get(medicationOrder, 'dateWritten'),
              end: null,
              type: 'point'
            })     
          }
        }
      });  
      MedicationStatements.find().forEach(function(medicationStatement){        
        //console.log('medicationStatement', medicationStatement);
        if(medicationStatement){
          if(get(medicationStatement, 'effectiveDateTime')){
            if(!dataSet.get(get(medicationStatement, 'id'))){
              dataSet.add({
                mongoId: get(medicationStatement, '_id', ''),
                id: get(medicationStatement, 'id') ? get(medicationStatement, 'id') : get(medicationStatement, '_id', ''),
                content: get(medicationStatement, 'medicationReference.display', ''),
                group: 'MedicationStatements',
                start: get(medicationStatement, 'effectiveDateTime', null),
                type: 'point'
              });  
            }
          }
          if(get(medicationStatement, 'effectivePeriod')){
            if(!dataSet.get(get(medicationStatement, 'id'))){
              dataSet.add({
                mongoId: get(medicationStatement, '_id', ''),
                id: get(medicationStatement, 'id') ? get(medicationStatement, 'id') : get(medicationStatement, '_id', ''),
                content: get(medicationStatement, 'medicationReference.display', ''),
                group: 'MedicationStatements',
                start: get(medicationStatement, 'effectivePeriod.start', null),
                end: get(medicationStatement, 'effectivePeriod.end', null),
                type: 'range',
                style: "background-color: white; border-color: lightgray;"
              });  
            }
          }  
        }
      });  
      Observations.find().forEach(function(observation){
        //console.log('observation', observation);

        let contextText = '';
        if(get(observation, 'code.text')){
          contextText = get(observation, 'code.text');
        }
        if(get(observation, 'code.coding[0].display')){
          contextText = get(observation, 'code.coding[0].display'); 
        }
        if(get(observation, 'valueQuantity.value')){
          contextText = contextText + ' - ' + get(observation, 'valueQuantity.value') 
        }
        if(get(observation, 'valueQuantity.unit')){
          contextText = contextText + ' ' + get(observation, 'valueQuantity.unit') 
        }

        if(get(observation, 'effectiveDateTime')){
          //console.log('observation.effectiveDateTime', moment(get(observation, 'effectiveDateTime', null)).format('YYYY-MM-DD'));
          if(!dataSet.get(get(observation, 'id'))){
            dataSet.add({
              mongoId: get(observation, '_id', ''),
              id: get(observation, 'id') ? get(observation, 'id') : get(observation, '_id', ''),
              content: contextText, 
              group: 'Observations',
              start: moment(observation.effectiveDateTime).format('YYYY-MM-DD'),
              type: 'point'
            })     
          }
        }
      });  
      Procedures.find().forEach(function(procedure){        
        //console.log('procedure', procedure);
        if(procedure){
          if(!dataSet.get(get(procedure, 'id'))){
            dataSet.add({
              //content: get(procedure, 'identifier[0].value') ? get(procedure, 'identifier[0].value') : get(procedure, 'code.text'),
              mongoId: get(procedure, '_id', ''),
              id: get(procedure, 'id') ? get(procedure, 'id') : get(procedure, '_id', ''),
              content:  get(procedure, 'code.text'),
              group: 'Procedures',
              start: get(procedure, 'performedDateTime'),
              type: 'point'
            })     
          }
        }
      });    
    }

    // const now = moment().minutes(0).seconds(0).milliseconds(0);
    
    // create a data set with groups
    const names = ['Conditions', 'MedicationOrders', 'Observations', 'Procedures', "CarePlans", "Allergies", "Immunizations"]
    for (let g = 0; g < names.length; g++) {
      data.groups.push({ id: names[g], content: names[g] })
    }

    data.items = dataSet.get();

    console.log('TimelineSidescrollPage.data', data)
    return data;
  }
  handleCloseDialog(){
    Session.set('timelineDialogOpen', false);
  }
  openDialog(props){
    console.log('openDialog', props)
    let activeTimelineItem = dataSet.get(props.items[0]);
    console.log('activeTimelineItem', activeTimelineItem);

    let activeTimelineRecord
    if(activeTimelineItem.group){
      activeTimelineRecord = Mongo.Collection.get(activeTimelineItem.group).findOne({_id: activeTimelineItem.id})
      console.log('activeTimelineRecord', activeTimelineRecord)
      Session.set('activeTimelineResource', activeTimelineRecord);
      Session.set('activeTimelineResourceType', activeTimelineItem.group);
      Session.set('activeTimelineResourceDescription', activeTimelineItem.content);
    }
    
    Session.set('timelineDialogOpen', true);
    
    props.event.preventDefault();
    props.event.stopPropagation();
  }
  preventRedraw(props){
    if(props && props.event){
      props.event.preventDefault();
      props.event.stopPropagation();  
    }
  }
  render(){

    let catchActions = [
      // <FlatButton
      //   label="Send SMS Message"
      //   primary={true}
      //   keyboardFocused={true}
      //   onClick={this.sendSmsMessage}
      // />,
      <FlatButton
        label="Cancel"
        primary={true}
        onClick={this.handleCloseDialog}
      />
    ];

    let self = this;

    let dialogContent;
    let resourceType = ''
    let dialogTitle = ''
    switch (this.data.activeTimelineResourceType) {
      case 'CarePlans':
        resourceType = 'CarePlan'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <CarePlanDetail 
          showHints={false}
          careplan={ get(this, 'data.activeTimelineResource') }
          careplanId={ get(this, 'data.activeTimelineResource.id') }
        />        
        break;
      case 'Conditions':
        resourceType = 'Condition'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ConditionDetail 
          showHints={false}
          showPatientInputs={false}
          condition={ get(this, 'data.activeTimelineResource') }
          conditionId={ get(this, 'data.activeTimelineResource.id') }
        />        
        break;
      case 'Observations':
        resourceType = 'Observation'
        dialogTitle = get(this, 'data.activeTimelineResourceDescription');
        dialogContent = <ObservationDetail
          displayDatePicker={true} 
          displayBarcodes={false}
          showHints={false}
          showPatientInputs={false}
          observation={ get(this, 'data.activeTimelineResource') }
          observationId={ get(this, 'data.activeTimelineResource.id') }
        />
        break;
      default:
        break;
    }

    
    return(
      <div id="horizontalTimeline" style={{paddingTop: '64px', paddingBottom: '64px', backgroundColor: '#f3f3f3'}}>
        { this.timeline }
        <Dialog
            actions={catchActions}
            modal={false}
            open={this.data.timelineDialogOpen}
            // onRequestClose={this.handleCloseDialog}
          >
            <CardTitle title={dialogTitle} subtitle={resourceType} />
            <CardText>
              { dialogContent }
            </CardText>
          </Dialog>
      </div>
    );
  }
}
ReactMixin(TimelineSidescrollPage.prototype, ReactMeteorData);
export default TimelineSidescrollPage;

