
import { Col, Grid, Row } from 'react-bootstrap';
import { CardHeader, CardText, CardTitle, CardActions, Tab, Tabs, FlatButton } from 'material-ui';
import React, {Component} from 'react'
import { get, has, sortBy } from 'lodash';
import { ReactMeteorData } from 'meteor/react-meteor-data';
import ReactMixin from 'react-mixin';
import { TimelineEvent } from 'react-event-timeline'

import { Glass, GlassCard, VerticalCanvas, FullPageCanvas, DynamicSpacer } from 'meteor/clinical:glass-ui';

import { ConditionsTable } from 'meteor/clinical:hl7-resource-condition';
import { GoalsTable } from 'meteor/clinical:hl7-resource-goal';
import { MedicationsTable } from 'meteor/clinical:hl7-resource-medication';

Session.setDefault('carePlanDesignerTabIndex', 0);

export class CarePlanDesignerPage extends React.Component {
  constructor(props) {
    super(props);
  }

  getMeteorData() {
    let data = {
      style: {
        tab: {
          borderBottom: '1px solid lightgray',
          borderRight: 'none',
          width: '200px'
        }
      },
      tabIndex: Session.get('carePlanDesignerTabIndex'),
      ccd: null,
      timelineEntries: []
    }

    data.style = Glass.blur(data.style);
    data.style.appbar = Glass.darkroom(data.style.appbar);
    data.style.tab = Glass.darkroom(data.style.tab);

    if(process.env.NODE_ENV === "test") console.log("CarePlanDesignerPage[data]", data);
    return data;
  };
  handleTabChange(index){
    Session.set('carePlanDesignerTabIndex', index);
  }
  render(){
    

    return(
      <div id="CarePlanDesignerPage">
        <FullPageCanvas >
          <Row>
            <Col md={6}>
              <GlassCard>
                <CardTitle title='Jane Doe' subtitle='Dec 1st, 1980' />
              </GlassCard>
              <DynamicSpacer />
              
              <GlassCard>
                <CardTitle title='Addresses' />
                <CardText>
                  <ConditionsTable />
                </CardText>
                <CardActions>
                  <FlatButton label='Add' />
                </CardActions>
              </GlassCard>
              <DynamicSpacer />
              
              <GlassCard>
                <CardTitle title='Goals' />
                <CardText>
                  <GoalsTable />
                </CardText>
                <CardActions>
                  <FlatButton label='Add' />
                </CardActions>
              </GlassCard>
              <DynamicSpacer />
              
              <GlassCard>
                <CardTitle title='Medications' />
                <CardText>
                  <MedicationsTable />
                </CardText>
                <CardActions>
                  <FlatButton label='Add' />
                </CardActions>
              </GlassCard>
            </Col>

            <Col md={6}>
              <GlassCard>
                <Tabs id='carePlanDesignerTabs' default value={this.data.tabIndex} onChange={this.handleTabChange} initialSelectedIndex={0}>
                  <Tab label='Conditions' style={this.data.style.tab} onActive={ this.handleTabChange } value={0}>
                    Foo
                  </Tab>
                  <Tab label='Goals' onActive={this.handleTabChange} style={this.data.style.tab} value={1}>
                    Bar
                  </Tab>
                  <Tab label='Detail' onActive={this.handleTabChange} style={this.data.style.tab} value={2}>
                    Baz
                  </Tab>
                </Tabs>
              </GlassCard>              
            </Col>

          </Row>
        </FullPageCanvas>
      </div>
    );
  }
}
ReactMixin(CarePlanDesignerPage.prototype, ReactMeteorData);
export default CarePlanDesignerPage;


